import React from 'react';
import format from 'date-fns/format'

import Box from 'components/Box'
import Text from 'components/Text'
import Flex from 'components/Flex'
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

const DateCard = ({ date, children, ...props }) => {
  return (
    <Box
      mx={mobileOrDesktop('1em', '0.5em')}
      mt="1em"
      pb={mobileOrDesktop('2em', '1em')}
      boxShadow="-2px 3px 2px rgba(0,0,0,0.1)"
      bg="white"
      borderRadius="1em"
      {...props}
    >
      <Flex
        bg="noteBg"
        p="1em"
        borderRadius="1em 1em 0 0"
        height="4em"
        color="white"
        alignItems="center"
      >
        <Box transform="rotate(90deg)" mr="0.5em">▲</Box>
        <Text fontSize="1.25em">{format(date, 'YYYY/MM/DD')}</Text>
      </Flex>
      {children}
    </Box>
  );
};

DateCard.defaultProps = {
  date: new Date()
}

export default DateCard;
