import React, { useState } from 'react';
import Measure from 'react-measure'

import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Yes from 'components/Yes';
import No from 'components/No';
import Button from 'components/Button';
import Circle from 'components/Circle';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import BodyPoints from './BodyPoints'
import Title from './Title'

import { options } from './Step1'
import { questions, nameId } from './Step2'

const renderDot = (dot, i) => (
  <Flex width={1 / 3} alignItems="center" mt="1em" key={i} px="1em">
    <Box width="2.5em" mr="0.5em">
      <Circle bg="orange" color="white" fontSize="1.75em">{i + 1}</Circle>
    </Box>
    <Box borderLeft="2px solid" borderColor="lightBgPurple" px="1em">
      <Text.inline mr="1em">持續天數</Text.inline>
      <Text.inline mr="1em">{options[dot.duration].label}</Text.inline>
      <Text mt="0.75em">症狀描述</Text>
      <Text fontSize="1.25em">{dot.pain}</Text>
    </Box>
  </Flex>
)

const MemoResult = ({ dots, answers, onEdit, onConfirm }) => {
  const [dimensions, setDimension] = useState({})

  return (
    <div>
      <Title mt="2em" title="我的看診備忘錄" />
      <Box
        my="2em"
        mx={mobileOrDesktop('10%', '5%')}
      >
        <Flex
          position="relative"
          height={dimensions.height}
          alignItems="center"
          flexWrap="wrap"
          mx="-1em"
        >
          {dots.slice(0, 4).reduce((all, dot, i) => all.concat([
            renderDot(dot, i),
            i % 2 ? null : <Box key={`space-${i}`} width={1 / 3} px="1em" />,
          ]), [])}
          <Measure bounds onResize={({ bounds }) => setDimension(bounds)}>
            {({ measureRef }) => (
              <Box
                position="absolute"
                top="0"
                left="33%"
                right="33%"
                bg="bgOrange"
                p="1em"
                borderRadius="1em"
                ref={measureRef}
              >
                <BodyPoints dots={dots} />
              </Box>
            )}
          </Measure>
        </Flex>
        <Flex
          alignItems="center"
          flexWrap="wrap"
          mt="1em"
          mx="-1em"
        >
          {dots.slice(4).map((dot, i) => renderDot(dot, i + 4))}
        </Flex>
      </Box>
      <Box mx={mobileOrDesktop('10%', '5%')}>
        <Flex mt="1em" flexWrap="wrap" borderTop="2px solid" borderBottom="2px solid" borderColor="lightBgPurple" py="2em">
          {questions.map((q, i) => (
            <Box width={mobileOrDesktop(1, 1/3)} borderLeft="2px solid" borderColor="lightBgPurple" px="1em" position="relative" key={i}>
              <Box>{q}</Box>
              <Flex alignItems="center" py={mobileOrDesktop('0.5em', '1em')}>
                <Box width="2em" color="orange" mr="1em" textAlign="left">{answers[nameId[i]] === '沒有' ? <No /> : <Yes />}</Box>
                <Box>{answers[nameId[i]]}</Box>
                <Box position="absolute" left="0" top="0" transform="translateX(-55%)">
                  <Circle width="1em" bg="lightBgPurple" />
                </Box>
              </Flex>
            </Box>
          ))}
        </Flex>
        <Flex justifyContent="center" my="2em">
          {onEdit && <Button mx="0.5em" px={mobileOrDesktop('2.5em', '3.5em')} onClick={onEdit}>修改</Button>}
          {onConfirm && <Button mx="0.5em" px={mobileOrDesktop('2.5em', '3.5em')} onClick={onConfirm}>確認</Button>}
        </Flex>
      </Box>
    </div>
  );
};

export default MemoResult;
