import React, { PureComponent } from 'react';
import { compose } from 'redux'
import { navigate } from 'gatsby';

import Progress from 'components/Progress';
import Container from 'components/Container';

import creatingHoc from './creatingHoc'
import storingHoc from './storingHoc'
import Layout from '../Layout';
import MemoResult from './MemoResult';
import StepNumber from './StepNumber'
import DateCard from './DateCard'

class Step3 extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      dots: props.dots,
      answers: props.answers,
    }
  }

  handleComfirm = () => {
    const { dots, answers, addMemo, clearData } = this.props
    navigate('/memo/notice')
    addMemo({ answers, dots })
    clearData()
  }

  render() {
    const { dots, answers } = this.state
    return (
      <Layout bg="lightBgOrange">
        <StepNumber>3</StepNumber>
        <Container py="2em" position="relative">
          <Progress step={3} />
          <DateCard>
            <MemoResult
              dots={dots}
              answers={answers}
              onConfirm={this.handleComfirm}
              onEdit={() => navigate('/memo/new/1')}
            />
          </DateCard>
        </Container>
      </Layout>
    );
  }
}

export default compose(creatingHoc, storingHoc)(Step3);
